"use client"

import { useState, useEffect, useRef } from "react"
import { useParams, useRouter } from "next/navigation"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Textarea } from "@/components/ui/textarea"
import { ArrowLeft, Download, Edit2, Save, X, FileText, Stethoscope } from "lucide-react"
import Link from "next/link"

interface SOAPNote {
  id: string
  subjective: string | null
  objective: string | null
  assessment: string | null
  plan: string | null
  icd10_codes: string | null
  cpt_codes: string | null
}

interface Encounter {
  id: string
  physician_id: string
  patient_id_hash: string
  chief_complaint: string
  encounter_type: string
  encounter_date: string
  transcription: string | null
  audio_duration_seconds: number | null
  soap_note: SOAPNote | null
  created_at: string
  updated_at: string
}

export default function EncounterDetail() {
  const params = useParams()
  const router = useRouter()
  const encounterId = params.id as string
  const contentRef = useRef<HTMLDivElement>(null)

  const [encounter, setEncounter] = useState<Encounter | null>(null)
  const [isLoading, setIsLoading] = useState(true)
  const [error, setError] = useState("")
  const [isEditing, setIsEditing] = useState(false)
  const [editedSOAP, setEditedSOAP] = useState({
    subjective: "",
    objective: "",
    assessment: "",
    plan: "",
  })

  useEffect(() => {
    if (!encounterId) return

    const fetchEncounter = async () => {
      try {
        console.log(`[v0] Fetching encounter: ${encounterId}`)
        const response = await fetch(`/api/v1/encounters/${encounterId}`)
        if (!response.ok) {
          throw new Error("Failed to fetch encounter")
        }
        const data = await response.json()
        setEncounter(data)
        if (data.soap_note) {
          setEditedSOAP({
            subjective: data.soap_note.subjective || "",
            objective: data.soap_note.objective || "",
            assessment: data.soap_note.assessment || "",
            plan: data.soap_note.plan || "",
          })
        }
      } catch (err) {
        setError("Failed to load encounter details")
        console.error("[v0] Encounter fetch error:", err)
      } finally {
        setIsLoading(false)
      }
    }

    fetchEncounter()
  }, [encounterId])

  const handleExportPDF = async () => {
    if (!encounter || !contentRef.current) return

    try {
      const html2pdf = (await import("html2pdf.js")).default

      const opt = {
        margin: 10,
        filename: `encounter-${encounter.id}.pdf`,
        image: { type: "jpeg", quality: 0.98 },
        html2canvas: { scale: 2 },
        jsPDF: { unit: "mm", format: "a4", orientation: "portrait" },
      }

      html2pdf().set(opt).from(contentRef.current).save()
    } catch (err) {
      console.error("[v0] PDF export error:", err)
      alert("Failed to export PDF. Please try again.")
    }
  }

  const handleSaveEdit = async () => {
    if (!encounter?.soap_note) return

    try {
      const response = await fetch(`/api/v1/soap/${encounter.soap_note.id}`, {
        method: "PUT",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(editedSOAP),
      })

      if (!response.ok) throw new Error("Failed to save changes")

      const updatedSOAP = await response.json()
      setEncounter({
        ...encounter,
        soap_note: { ...encounter.soap_note, ...updatedSOAP },
      })
      setIsEditing(false)
    } catch (err) {
      console.error("[v0] Save error:", err)
      alert("Failed to save changes. Please try again.")
    }
  }

  const handleCancelEdit = () => {
    if (encounter?.soap_note) {
      setEditedSOAP({
        subjective: encounter.soap_note.subjective || "",
        objective: encounter.soap_note.objective || "",
        assessment: encounter.soap_note.assessment || "",
        plan: encounter.soap_note.plan || "",
      })
    }
    setIsEditing(false)
  }

  if (isLoading) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-slate-50 to-blue-50 p-8">
        <div className="mx-auto max-w-5xl">
          <div className="flex items-center justify-center py-20">
            <div className="text-center">
              <Stethoscope className="mx-auto h-12 w-12 animate-pulse text-blue-600" />
              <p className="mt-4 text-slate-600">Loading encounter...</p>
            </div>
          </div>
        </div>
      </div>
    )
  }

  if (error || !encounter) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-slate-50 to-blue-50 p-8">
        <div className="mx-auto max-w-5xl">
          <div className="rounded-lg border border-red-200 bg-red-50 p-6 text-red-700">
            <p className="font-semibold">{error || "Encounter not found"}</p>
          </div>
          <Link href="/encounters" className="mt-6 inline-block">
            <Button variant="outline" className="gap-2 bg-transparent">
              <ArrowLeft className="h-4 w-4" />
              Back to Encounters
            </Button>
          </Link>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 to-blue-50">
      <div className="mx-auto max-w-5xl px-4 py-8">
        {/* Header */}
        <div className="mb-8">
          <Link href="/encounters">
            <Button variant="ghost" className="mb-4 gap-2 hover:bg-white/50">
              <ArrowLeft className="h-4 w-4" />
              Back to Encounters
            </Button>
          </Link>

          <div className="flex items-start justify-between gap-4">
            <div>
              <div className="flex items-center gap-3">
                <FileText className="h-8 w-8 text-blue-600" />
                <h1 className="text-3xl font-bold text-slate-900">{encounter.chief_complaint || "Encounter"}</h1>
              </div>
              <p className="mt-2 text-slate-600">
                {new Date(encounter.created_at).toLocaleDateString("en-US", {
                  year: "numeric",
                  month: "long",
                  day: "numeric",
                  hour: "2-digit",
                  minute: "2-digit",
                })}
              </p>
            </div>

            <div className="flex gap-2">
              {!isEditing ? (
                <>
                  <Button onClick={handleExportPDF} variant="outline" className="gap-2 bg-white hover:bg-slate-50">
                    <Download className="h-4 w-4" />
                    Export PDF
                  </Button>
                  {encounter.soap_note && (
                    <Button onClick={() => setIsEditing(true)} className="gap-2 bg-blue-600 hover:bg-blue-700">
                      <Edit2 className="h-4 w-4" />
                      Edit
                    </Button>
                  )}
                </>
              ) : (
                <>
                  <Button onClick={handleCancelEdit} variant="outline" className="gap-2 bg-white hover:bg-slate-50">
                    <X className="h-4 w-4" />
                    Cancel
                  </Button>
                  <Button onClick={handleSaveEdit} className="gap-2 bg-green-600 hover:bg-green-700">
                    <Save className="h-4 w-4" />
                    Save Changes
                  </Button>
                </>
              )}
            </div>
          </div>
        </div>

        {/* Content for PDF export */}
        <div ref={contentRef} className="space-y-6">
          {/* Basic Info */}
          <Card className="border-slate-200 bg-white shadow-sm">
            <CardHeader className="border-b border-slate-100">
              <CardTitle className="text-xl">Encounter Information</CardTitle>
            </CardHeader>
            <CardContent className="pt-6">
              <div className="grid gap-6 md:grid-cols-2">
                <div className="space-y-1">
                  <p className="text-sm font-medium text-slate-500">Physician ID</p>
                  <p className="text-lg font-semibold text-slate-900">{encounter.physician_id}</p>
                </div>
                <div className="space-y-1">
                  <p className="text-sm font-medium text-slate-500">Encounter Type</p>
                  <p className="text-lg font-semibold capitalize text-slate-900">
                    {encounter.encounter_type.replace(/_/g, " ")}
                  </p>
                </div>
                <div className="space-y-1">
                  <p className="text-sm font-medium text-slate-500">Chief Complaint</p>
                  <p className="text-lg font-semibold text-slate-900">{encounter.chief_complaint}</p>
                </div>
                <div className="space-y-1">
                  <p className="text-sm font-medium text-slate-500">Audio Duration</p>
                  <p className="text-lg font-semibold text-slate-900">
                    {encounter.audio_duration_seconds ? `${encounter.audio_duration_seconds}s` : "N/A"}
                  </p>
                </div>
              </div>
            </CardContent>
          </Card>

          {/* Transcription */}
          {encounter.transcription && (
            <Card className="border-slate-200 bg-white shadow-sm">
              <CardHeader className="border-b border-slate-100">
                <CardTitle className="text-xl">Transcription</CardTitle>
                <CardDescription>Audio transcribed by Azure Whisper AI</CardDescription>
              </CardHeader>
              <CardContent className="pt-6">
                <div className="rounded-lg bg-slate-50 p-4 border border-slate-200">
                  <p className="whitespace-pre-wrap leading-relaxed text-slate-700">{encounter.transcription}</p>
                </div>
              </CardContent>
            </Card>
          )}

          {/* SOAP Notes */}
          {encounter.soap_note ? (
            <div className="space-y-6">
              {/* Medical Codes section */}
              <Card className="border-slate-200 bg-white shadow-sm">
                <CardHeader className="border-b border-slate-100">
                  <CardTitle className="text-xl">Medical Coding</CardTitle>
                  <CardDescription>ICD-10 diagnosis codes and CPT procedure codes</CardDescription>
                </CardHeader>
                <CardContent className="pt-6">
                  <div className="grid gap-6 md:grid-cols-2">
                    <div>
                      <p className="mb-3 text-sm font-semibold text-slate-700">ICD-10 Diagnosis Codes</p>
                      <div className="space-y-2">
                        {encounter.soap_note.icd10_codes ? (
                          JSON.parse(encounter.soap_note.icd10_codes).map((code: string, idx: number) => (
                            <div
                              key={idx}
                              className="flex items-center gap-2 rounded-lg bg-blue-50 border border-blue-200 px-4 py-3"
                            >
                              <span className="font-mono text-sm font-bold text-blue-900">{code}</span>
                            </div>
                          ))
                        ) : (
                          <p className="text-sm text-slate-500 italic">No ICD-10 codes generated</p>
                        )}
                      </div>
                    </div>
                    <div>
                      <p className="mb-3 text-sm font-semibold text-slate-700">CPT Procedure Codes</p>
                      <div className="space-y-2">
                        {encounter.soap_note.cpt_codes ? (
                          JSON.parse(encounter.soap_note.cpt_codes).map((code: string, idx: number) => (
                            <div
                              key={idx}
                              className="flex items-center gap-2 rounded-lg bg-green-50 border border-green-200 px-4 py-3"
                            >
                              <span className="font-mono text-sm font-bold text-green-900">{code}</span>
                            </div>
                          ))
                        ) : (
                          <p className="text-sm text-slate-500 italic">No CPT codes generated</p>
                        )}
                      </div>
                    </div>
                  </div>
                </CardContent>
              </Card>

              {/* SOAP sections */}
              <div className="grid gap-6 md:grid-cols-2">
                {/* Subjective */}
                <Card className="border-slate-200 bg-white shadow-sm">
                  <CardHeader className="border-b border-slate-100 bg-blue-50/50">
                    <CardTitle className="text-lg font-bold text-blue-900">Subjective (S)</CardTitle>
                    <CardDescription className="text-blue-700">Patient's reported symptoms and history</CardDescription>
                  </CardHeader>
                  <CardContent className="pt-4">
                    {isEditing ? (
                      <Textarea
                        value={editedSOAP.subjective}
                        onChange={(e) => setEditedSOAP({ ...editedSOAP, subjective: e.target.value })}
                        className="min-h-[150px] text-sm"
                        placeholder="Enter subjective findings..."
                      />
                    ) : (
                      <p className="whitespace-pre-wrap text-sm leading-relaxed text-slate-700">
                        {encounter.soap_note.subjective || "No subjective data"}
                      </p>
                    )}
                  </CardContent>
                </Card>

                {/* Objective */}
                <Card className="border-slate-200 bg-white shadow-sm">
                  <CardHeader className="border-b border-slate-100 bg-purple-50/50">
                    <CardTitle className="text-lg font-bold text-purple-900">Objective (O)</CardTitle>
                    <CardDescription className="text-purple-700">
                      Clinical observations and measurements
                    </CardDescription>
                  </CardHeader>
                  <CardContent className="pt-4">
                    {isEditing ? (
                      <Textarea
                        value={editedSOAP.objective}
                        onChange={(e) => setEditedSOAP({ ...editedSOAP, objective: e.target.value })}
                        className="min-h-[150px] text-sm"
                        placeholder="Enter objective findings..."
                      />
                    ) : (
                      <p className="whitespace-pre-wrap text-sm leading-relaxed text-slate-700">
                        {encounter.soap_note.objective || "No objective data"}
                      </p>
                    )}
                  </CardContent>
                </Card>

                {/* Assessment */}
                <Card className="border-slate-200 bg-white shadow-sm">
                  <CardHeader className="border-b border-slate-100 bg-amber-50/50">
                    <CardTitle className="text-lg font-bold text-amber-900">Assessment (A)</CardTitle>
                    <CardDescription className="text-amber-700">Diagnosis and clinical impression</CardDescription>
                  </CardHeader>
                  <CardContent className="pt-4">
                    {isEditing ? (
                      <Textarea
                        value={editedSOAP.assessment}
                        onChange={(e) => setEditedSOAP({ ...editedSOAP, assessment: e.target.value })}
                        className="min-h-[150px] text-sm"
                        placeholder="Enter assessment..."
                      />
                    ) : (
                      <p className="whitespace-pre-wrap text-sm leading-relaxed text-slate-700">
                        {encounter.soap_note.assessment || "No assessment data"}
                      </p>
                    )}
                  </CardContent>
                </Card>

                {/* Plan */}
                <Card className="border-slate-200 bg-white shadow-sm">
                  <CardHeader className="border-b border-slate-100 bg-green-50/50">
                    <CardTitle className="text-lg font-bold text-green-900">Plan (P)</CardTitle>
                    <CardDescription className="text-green-700">Treatment plan and follow-up</CardDescription>
                  </CardHeader>
                  <CardContent className="pt-4">
                    {isEditing ? (
                      <Textarea
                        value={editedSOAP.plan}
                        onChange={(e) => setEditedSOAP({ ...editedSOAP, plan: e.target.value })}
                        className="min-h-[150px] text-sm"
                        placeholder="Enter treatment plan..."
                      />
                    ) : (
                      <p className="whitespace-pre-wrap text-sm leading-relaxed text-slate-700">
                        {encounter.soap_note.plan || "No plan data"}
                      </p>
                    )}
                  </CardContent>
                </Card>
              </div>
            </div>
          ) : (
            <Card className="border-slate-200 bg-white shadow-sm">
              <CardContent className="py-12">
                <div className="text-center">
                  <Stethoscope className="mx-auto h-12 w-12 text-slate-300" />
                  <p className="mt-4 text-slate-600">No SOAP note generated yet</p>
                </div>
              </CardContent>
            </Card>
          )}
        </div>
      </div>
    </div>
  )
}
